import { BehaviorSubject } from "rxjs";
// 1) Import image
import bear from '../assets/images/bear.svg';
import grass from '../assets/images/grass.jpg';
export class ImageService {
    constructor() {
        // 3) Load image to files object
        this.files = {
            bear: this.load(bear),
            grass: this.load(grass),
        };
        this.filesCount = Object.keys(this.files).length;
        this.loadedFilesCount = 0;
        this.isLoadedSbj = new BehaviorSubject(false);
        this.isLoaded$ = this.isLoadedSbj.asObservable();
    }
    load(file) {
        const img = new Image();
        img.onload = this.fileLoaded.bind(this);
        img.src = file;
        return img;
    }
    fileLoaded() {
        if (++this.loadedFilesCount >= this.filesCount)
            this.isLoadedSbj.next(true);
    }
}
