import { Player } from "../objects/player/player.object";
import { Wall } from "../objects/wall/wall.object";
export class ObjectService {
    constructor(imageService, canvasService) {
        this.imageService = imageService;
        this.canvasService = canvasService;
        this.staticObjects = [];
        this.dynamicObjects = [];
    }
    init(objects) {
        this.clearObjects();
        for (let obj of objects) {
            switch (obj.type) {
                case 'wall':
                    this.staticObjects.push(new Wall(obj, this.imageService, this.canvasService));
                    break;
                case 'player':
                    this.dynamicObjects.push(new Player(obj, this.imageService, this.canvasService));
                    break;
            }
        }
    }
    renderObjects(fps) {
        this.staticObjects.forEach(o => o.render());
        this.dynamicObjects.forEach(o => o.renderEntity(fps));
    }
    clearObjects() {
        this.staticObjects = [];
        this.dynamicObjects = [];
    }
}
