import CanvasService from "./services/canvas.service";
import { ImageService } from "./services/image.service";
import { ObjectService } from "./services/object.service";
import { GameLoopService } from "./services/game-loop.service";
export default class Platformer2D {
    constructor(canvas, config) {
        this.config = config;
        this.imageService = new ImageService();
        this.canvasService = new CanvasService(canvas, config);
        this.objectService = new ObjectService(this.imageService, this.canvasService);
        this.gameLoopService = new GameLoopService(this.canvasService, this.objectService);
        this.init();
    }
    init() {
        const sub = this.imageService.isLoaded$.subscribe(isLoaded => {
            if (isLoaded) {
                sub.unsubscribe();
                this.objectService.init(this.config.objects);
                this.afterInit();
            }
        });
    }
    afterInit() {
        this.gameLoopService.start();
    }
}
;
