export class BasicObject {
    constructor(config, imageService, canvasService) {
        this.config = config;
        this.imageService = imageService;
        this.canvasService = canvasService;
        this.object = Object.assign(Object.assign({}, config), { model: this.imageService.files[config.modelName], isModelFlipped: false });
    }
    render() {
        this.drawObject();
    }
    ;
    drawObject() {
        const modelCoords = [
            ...this.config.coords, ...this.config.modelSize
        ];
        if (this.object.isModelFlipped)
            this.canvasService.drawFlippedImage(this.object.model, modelCoords);
        else
            this.canvasService.drawImage(this.object.model, modelCoords);
    }
}
